.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  background: #333;
  color: #fff;
  width: auto;
}
#edlogo{
  position: relative;
  right: 1180px;
  width: 150px;
  height: 100px;
}
#h4welcome{
  position: relative;
  left: 170px;
  font-size: medium;
}
#edcorp{
  position: relative;
  left: 90px;
}
.navbar .logo {
  font-size: 1.5rem;
  position: relative;
  left: 5px;
  font-weight: bold;
  text-decoration: underline;
  cursor: not-allowed;
}

.navbar .nav-links {
  list-style: none;
  display: flex;
  position: relative;
  left: 170px;
  
}

.navbar .nav-links li {
  margin-left: 1rem;
}

.navbar .nav-links a {
  color: white;
  text-decoration: none;
  border: solid 1.5px #333;
  padding: 5px;
}
.nav-links a:hover{
  border-color: lightgray;
}


#log_regist_edit{
  border: solid 1.5px white;
  padding: 8px;
  border-radius: 25px;
  
  
}
#log_regist_edit:hover{

  
  border-color: lightgreen; 
  transform: scale(1.05);
}


